
#app #blur-div {
  position: relative;
  margin: 0 auto;
  overflow: hidden;
}
#app #blur-div #blur-image {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  margin: 0 auto;
  -webkit-filter: blur(15px);
          filter: blur(15px);
  z-index: 0;
}
#app #blur-div #canvas {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  margin: 0 auto;
  z-index: 100;
}
#app #blur-div .button {
  display: block;
  position: absolute;
  z-index: 200;
  width: 100px;
  height: 30px;
  color: #fff;
  text-decoration: none;
  text-align: center;
  line-height: 30px;
  border-radius: 5px;
}
#app #blur-div .button.reset-button {
  left: 50px;
  bottom: 50px;
  background-color: #058;
}
#app #blur-div .button.reset-button:hover {
  background-color: #047;
}
#app #blur-div .button.show-button {
  right: 50px;
  bottom: 50px;
  background-color: #085;
}
#app #blur-div .button.show-button:hover {
  background-color: #074;
}


